.project>img{
    width: 350px;
    height:250px;
}

.project{
    margin: 15px;
    width: 350px;
    height: 250px;
    transition: transform 100ms ease-in;
}

.project:hover{
    background-color: #D4AF37;
    transform: scale(1.05);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.projectContent{
    padding: 10px;
}

.projectContent> h4{
    padding-bottom: 5px;
}

.projectContent>p{
    font-size: 15px;
}