
@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300;600&display=swap');

.skillContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 200px 0;
}

.skillContainer__text{
    color: #d8d8d8;
    flex: 0.6;
    text-align: left;
    max-width: 500px;
    margin-bottom: 20px;
}

.skillContainer__text>h2{
    font-size: 30px;
    margin-top: 80px;
}

.skillContainer__image{
    flex:0.4;
    text-align: center;
    margin-right: 100px;
}

.skillContainer__image>img{
    width: 600px;
    height: auto;
    border-radius: 3px;
    object-fit: contain;
}

.skillContainer__skillset{
    font-family: 'Montserrat',sans-serif;
}

.skillContainer__skillset>h3{
    font-family: 'Montserrat',sans-serif;
    font-weight: 300;
    padding-bottom: 30px;
    padding-top: 10px;
}

