@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300;600&display=swap');
*{
    color:#38220F
}
.topContent{
    color: #38220F;
}

.topContent__Container{
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    top:300px;
    right:400px;
}

.topContent__Container{
    letter-spacing: 1px;
    text-align: left;
    padding-left: 4px;
    padding-top: 20px;
}

.topContent__Container>a>button{
    margin-top: 20px;
    text-align: left;
    margin-left: 5px;
    border:none;
    border-radius: 2px;
    outline: none;
}

.topContent__Container>h1{
    font-size: 50px;
    font-family: "Montserrat",sans-serif;
}

.topContent__Downloadbtn{
    color:#d8d8d8;
    background-color: #38220F;
    font-size: 16px;
    padding: 10px 40px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;
    margin-top: 10px;
}

.topContent__Projectsbtn{
    text-decoration-color: #38220F;
    color:#38220F;
    border: 1px solid #38220F !important;
    background-color: transparent;
    font-size: 16px;
    padding: 9px 55px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor:pointer;
    margin-top: 15px;
}

.topContent__Downloadbtn:hover{
    color:#38220F;
    background-color: #d8d8d8;
}

.topContent__Projectsbtn:hover{
    color:#38220F;
    background-color: #d8d8d8;
}

.top>img{
    display: flex;
    flex-direction: row;
    -ms-zoom-animation: 10%;
    width: 130px;
    height: 250px;
    padding-right: 50px;
}
