.contact{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 200px 0 100px;
    align-items: center;
}

.contact__container{
    border: 1px solid #38220F;
    width: 600px;
    height:200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px;
    margin-top: 20px;
}

.contact__container>p{
    font-size: 20px;
    text-align: left;
    padding-bottom: 30px;
}

.contact__container>p>span>b{
    color: #D4AF37 ;
}
 
.contact>h1{
    margin-top: 20px;
}

.contact_icons>a>button>span>.css-i4bv87-MuiSvgIcon-root{
    color:#D4AF37;
    font-size: 30px;
}

.contact_icons>a>button{
     color: #38220f !important;
}